import { Component, OnDestroy, OnInit, ViewEncapsulation, Inject } from '@angular/core';

import { DOCUMENT } from '@angular/common';

import { LearnConfigService } from 'src/@learn/services/config/config.service';
import { Layout } from 'src/app/layout/layout.types';
import { AppConfig, Theme } from 'src/app/core/config/app.config';

import { Router } from '@angular/router';
import { interval, Subject } from 'rxjs';
import { take, takeUntil } from 'rxjs/operators';

@Component({
    selector     : 'auth-sign-out',
    templateUrl  : './sign-out.component.html',
    styleUrls    : ['./sign-out.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class AuthSignOutComponent implements OnInit, OnDestroy
{
    countdown: number;
    countdownMapping: any;

    config: AppConfig;
    layout: Layout;
    theme: Theme;

    // Private
    private _unsubscribeAll: Subject<any>;

    /**
     * Constructor
     *
     * @param {AuthService} _authService
     * @param {Router} _router
     */
    constructor(
        @Inject(DOCUMENT) private _document: any,
        private _learnConfigService: LearnConfigService,
        private _router: Router
    )
    {
        // Set the private default
        this._unsubscribeAll = new Subject();

        // Set the defaults
        this.countdown = 10;
        this.countdownMapping = {
            '=1'   : '# second',
            'other': '# seconds'
        };
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Lifecycle hooks
    // -----------------------------------------------------------------------------------------------------

    /**
     * On init
     */
    ngOnInit(): void
    {

        document.querySelector('body').className = '';
        this._document.body.classList.add('learn-splash-screen-hidden');
        this._document.body.classList.add('learn-theme-light');


        // Get the duration
        const duration = this.countdown;

        // Redirect after the countdown
        interval(1000)
            .pipe(
                take(duration),
                takeUntil(this._unsubscribeAll)
            )
            .subscribe(() => {
                    this.countdown--;
                },
                () => {
                },
                () => {
                    this._router.navigate(['/login']);
                }
            );
    }

    /**
     * On destroy
     */
    ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }
}
